import React, {useRef, useState, useEffect} from 'react';
import './Step.scss';
import RightArrowIcon from './../icons/right-arrow.svg';
import LeftArrowIcon from './../icons/back-arrow.svg';
import {Link, useLocation, useParams} from "react-router-dom";
import { toMMSS } from './../utils/timerUtils';

export default function Step({}) {
    let interval = null;
    const [ data, setData ] = useState(null);
    const [ timer, setTimer ] = useState(0);
    const [ isTimerStarted, setTimerStarted] = useState(false);
    const [ instruction, setInstruction] = useState('');

    const params = useParams();
    const stepIndex = parseInt(params.stepIndex);

    useEffect(() => {
        import(`./../data/${params.type}`)
            .then((data) => {
                setData(data.default[stepIndex]);
                if (data.default[stepIndex].time) {
                    setTimer(data.default[stepIndex].time);
                }
            });
    }, [stepIndex]);

    const startTimer = (time) => {
        setTimerStarted(true);
        let t = time;
        let countdown = 30;
        interval = setInterval(() => {
            t = t - 1;

            console.log('t', t);
            console.log('countdown', countdown)

            if (t > (time - 15)) {
                setInstruction('Agitate');
            } else if (t <= (time -15)) {
                countdown = countdown - 1;
                if (countdown === 0) {
                    setInstruction('Invert');
                    countdown = 30;
                }
            } else if (t <= 5) {
                setInstruction('Pour out');
            } else {
                setInstruction('');
            }

            setTimer(t);

            if (t <= 0) {
                clearInterval(interval);
                setTimerStarted(false);
            }
        }, 1000);
    };

    return (
        <div className="step-wrapper">
            { data &&
                <div className={`step ${isTimerStarted ? 'active' : ''}`} onClick={() => !isTimerStarted && startTimer(data.time)}>
                    <h1>Step {stepIndex}: {data.name}</h1>
                    { <div className="timer">{timer > 0 && toMMSS(timer)}</div>}
                    <p className="instruction">{instruction}</p>
                </div>
            }
            <div className="arrows">
                <div className="arrow">
                    <Link to={`/${params.type}/step/${stepIndex-1}`}>
                        <img src={LeftArrowIcon} alt="" className="icon"/>
                    </Link>
                </div>
                <div className="arrow" onClick={() => {
                    clearInterval(interval)
                    setTimerStarted(false);
                }}>
                    <Link to={`/${params.type}/step/${stepIndex+1}`}>
                        <img src={RightArrowIcon} alt="" className="icon"/>
                    </Link>
                </div>
            </div>
        </div>
    );
}